.container {
  //추천 배경 색 : 3DB2FF;
  position: relative;
  max-width: 900px;
  min-width: 700px;
  width: 90%;
  margin: 0 auto;
  .problem {
    border-radius: 0.7em;
    margin-bottom: 0.3em;
    box-sizing: border-box;
    &.visible {
      display: block;
    }
    &.unvisible {
      display: none;
    }
    .question {
      //질문 디자인
      text-align: center;
      padding: 0.2em;
      font-size: 1.4em;
      font-weight: bold;
      width: 100%;
      height: 100%;
      background: #ffedda;
      box-sizing: border-box;
      position: relative;
      border-bottom: 2px solid #bd7e00;
    }

    .select {
      display: flex;
      background: white;
      div {
        //답안
        text-align: center;
        padding-top: 0.2em;
        font-size: 1.2em;
        width: 100%;
        height: 1.5em;
        background: #ffb830;
        box-sizing: border-box;

        //보기 간격 표시
        border-right: 2px solid #bd7e00;
        &:nth-child(4) {
          border-right: none;
        }

        &.answer {
          background: rgba(#ff2442, 0.7);
          font-weight: 700;
        }
      }
    }
  }
}
